.container {
  max-width: $site-width;
  margin: auto;
  height: 100%;
  padding: 0;
}

@media (max-width: 640px) {
  .nav__title {
    text-align: center;
  }

  .calculator {
    padding-bottom: 1.5rem;
    .form {
      flex-direction: column;
      padding-top: 1.5rem;

      &__group {
        margin: auto;
        width: 90%;

        &:first-of-type {
          width: 90%;
          margin: auto;
          height: 60px;
          margin-bottom: 1rem;
        }

        .input {
          &--amount {
            display: inline-block;
            width: 100%;
            padding: 1.1rem;
            font-size: 1.3rem;
          }

          &--search {
            height: 50px;
          }
        }

        .dropdown {
          width: 100%;
        }

        .dropdown-open {
          display: inline-block;
          width: 100%;
          display: flex;
          justify-content: space-between;
          padding: 0 1.1rem;
          margin-bottom: 1rem;

          img {
            width: 60px;
          }
        }
      }

      .btn {
        &--submit {
          display: inline-block;
          width: 90%;
          margin: auto;
          height: 60px;
        }

        &--swap {
          display: none;
        }

        &--search {
          height: 50px;
          img {
            width: 50px;
          }
        }
      }
    }

    .result {
      &__amount {
        padding: 1.8rem;
      }

      &__info {
        flex-direction: column;
        text-align: center;
      }
    }
  }

  .chart {
    height: 350px;

    &__header {
      width: 110px;
      top: 0px;
      left: 0px;
    }
  }
}
